import React from 'react';
import { Form, Input, Button, Checkbox } from 'antd';
import './Loginform.css';

const Login = (props) => {
  const onFinish = (values) => {
    console.log('Received values:', values);
  };

  return (
    <div className="login-container">
      <div className="login-form-container">
        <h2 className="login-title">Login</h2>
        <Form
          name="loginForm"
          initialValues={{ remember: true }}
          onFinish={onFinish}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: 'Please input your username!' }]}
          >
            <Input style={{width:'250PX',backgroundColor:props.issmall?'transparent':'white',color:props.issmall?'white':'black'}} placeholder="Username" />
          </Form.Item>

          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <Input.Password placeholder="Password"  style={{backgroundColor:props.issmall?'transparent':'white',color:props.issmall?'white':'black'}} />
          </Form.Item>

          <Form.Item>
            <Form.Item name="remember"  valuePropName="checked" noStyle>
              <Checkbox style={{color:'white'}}>Remember me</Checkbox>
            </Form.Item>

            <a className="login-form-forgot" href="#!">
              Forgot password
            </a>
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit" className="login-form-button" style={{backgroundColor:props.issmall?'transparent':'white',color:props.issmall?'white':'black'}}>
              Log in
            </Button>
            Or <a href="#!" style={{padding:'4px'}}>register now!</a>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default Login;
